@Injectable({
  providedIn: 'root'
})
export class NgRestoCartService {
  cartID:string;
  cart:BehaviorSubject<any>;
  modifires:BehaviorSubject<any>;

  modifiresMessage:BehaviorSubject<any>;
  messages:EventMessage[];

  constructor(private net:NetService,
              private eventer:EventerService) {
    this.cart = new BehaviorSubject({});
    this.modifires = new BehaviorSubject([]);
    this.modifiresMessage = new BehaviorSubject([]);

    this.initialStorage();

    this.modifiresMessage.subscribe(messages => this.messages = messages);
  }

  initialStorage() {

    this.cartID = this.getcartIDFromStorage();

    if (this.cartID) {
      this.net.get('/cart?cartId=' + this.cartID).subscribe(cart=> {
        this.cart.next(cart.cart);
      });
    }

    /*     this.restoStorageService.sub('localStorageService','cartID').subscribe(res=>{

     if(res.changeKey){
     console.log("event",res)
     this.net.get('/cart?cartId='+this.cartID).subscribe(cart=>{
     this.cart.next(cart);
     });}

     });; */


  }

  getcartIDFromStorage():string {
    return localStorage.getItem('cartID');
  }

  addDishToCart(data) {

    if (this.messages.length) {
      this.messages.forEach(message => {
        this.eventer.emitMessageEvent(message);
      });
      return;
    }

    this.net.put('/cart/add', data).subscribe(
      res=> {

        this.setcartIDFromStorage(res.cart.cartId);
        this.cart.next(res.cart);
        this.cartID = res.cart.cartId;

        /*this.eventer.emitMessageEvent(
         new EventMessage('success', 'Ð£ÑÐ¿ÐµÑ', 'ÐÐ»ÑÐ´Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¾ Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ñ')
         );*/

      }, error => {
        /*this.eventer.emitMessageEvent(
         new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ Ð±Ð»ÑÐ´Ð¾')
         )*/
      });
  }

  setDishCountToCart(dishId, amount) {
    this.net.post('/cart/set', {
      "dishId": dishId,
      "cartId": this.cartID,
      "amount": amount
    }).subscribe(
      res=> {

        this.setcartIDFromStorage(res.cart.cartId);
        this.cart.next(res.cart);
        this.cartID = res.cart.cartId;

        /*this.eventer.emitMessageEvent(
         new EventMessage('success', 'Ð£ÑÐ¿ÐµÑ', 'ÐÐ·Ð¼ÐµÐ½ÐµÐ½Ð¾ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾')
         );*/


      }, error => {
        /*this.eventer.emitMessageEvent(
         new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð¸Ð·Ð¼ÐµÐ½Ð¸ÑÑ ÐºÐ¾Ð»Ð¸ÑÐµÑÑÐ²Ð¾')
         )*/
      });
  }

  setDishComment(dishId, comment) {
    return this.net.post('/cart/setcomment', {
      "dishId": dishId,
      "cartId": this.cartID,
      "comment": comment
    }).pipe(tap(
      res=> {

        this.setcartIDFromStorage(res.cart.cartId);
        this.cart.next(res.cart);
        this.cartID = res.cart.cartId;

      }, error => {
        this.eventer.emitMessageEvent(
          new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð¸Ð·Ð¼ÐµÐ½Ð¸ÑÑ ÐºÐ¾Ð¼ÐµÐ½ÑÐ°ÑÐ¸Ð¹')
        )
      }
    ))

  }

  removeDishFromCart(dishId, amount) {
    this.net.put('/cart/remove', {
      "dishId": dishId,
      "cartId": this.cartID,
      "amount": amount
    }).subscribe(
      res=> {

        this.setcartIDFromStorage(res.cart.cartId);
        this.cart.next(res.cart);
        this.cartID = res.cart.cartId;
        /*this.eventer.emitMessageEvent(
         new EventMessage('success', 'Ð£ÑÐ¿ÐµÑ', 'ÐÐ»ÑÐ´Ð¾ ÑÑÐ¿ÐµÑÐ½Ð¾ ÑÐ´Ð°Ð»ÐµÐ½Ð¾')
         );*/

      }, error => {
        /*this.eventer.emitMessageEvent(
         new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ ÑÐ´Ð°Ð»Ð¸ÑÑ Ð±Ð»ÑÐ´Ð¾')
         )*/
      });

  }

  checkoutCart(data) {
    let order:Order = {
      cartId: this.cartID,
      address: {
        streetId: data.street.id,
        home: data.house,
        housing: data.housing,
        // index: "1278",
        entrance: data.entrance,
        floor: data.floor,
        apartment: data.apartment
      },

      customer: {
        phone: data.phone,
        name: data.name,
        mail: data.email || undefined
      }
    };
    return this.orderCart(order);

  }

  orderCart(data) {
    return this.net.post('/order', data)
      .pipe(
        tap(
          result => {
            this.setcartIDFromStorage(result.cart.cartId);
            this.cart.next(result.cart);
            this.cartID = result.cart.cartId;
            /*this.eventer.emitMessageEvent(
             new EventMessage('success', 'Ð£ÑÐ¿ÐµÑ', 'ÐÐ°ÐºÐ°Ð· ÑÐ¿ÐµÑÐ½Ð¾ Ð¾ÑÐ¾ÑÐ¼Ð»ÐµÐ½')
             );*/
          },
          error => {
            console.log("ÐÑÐ¸Ð±ÐºÐ° Ð¾ÑÐ¾ÑÐ¼Ð»ÐµÐ½Ð¸Ñ!", error);
            if (error.error && error.error.cart) {
              this.setcartIDFromStorage(error.error.cart.cartId);
              this.cart.next(error.error.cart);
              this.cartID = error.error.cart.cartId;
            }
            /*if (error.message) {
              this.eventer.emitMessageEvent(
                new EventMessage(error.message.type, error.message.title, error.message.body)
              );
            } else {
              this.eventer.emitMessageEvent(
                new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð¾ÑÐ¾ÑÐ¼Ð¸ÑÑ Ð·Ð°ÐºÐ°Ð·')
              );
            }*/
          }
        )
      );
  }

  checkStreetV2(data):Observable<any> {
    return this.net.post('/check', data)
      .pipe(
        tap(
          result => {
            this.setcartIDFromStorage(result.cart.cartId);
            this.cart.next(result.cart);
            this.cartID = result.cart.cartId;
            /*if (result.message) {
             this.eventer.emitMessageEvent(
             new EventMessage(
             result.message.type,
             result.message.title,
             result.message.body
             )
             );
             }*/
          },
          error => {
            console.error(error);
            //this.eventer.emitMessageEvent(
            //new EventMessage('error', 'ÐÑÐ¸Ð±ÐºÐ°', 'ÐÐµ ÑÐ´Ð°Ð»Ð¾ÑÑ Ð¾ÑÐ¾ÑÐ¼Ð¸ÑÑ Ð·Ð°ÐºÐ°Ð·')
            //)
          }
        )
      );
  }

  checkStreet(data):void {

    this.net.post('/check', data).subscribe(
      res => {
        this.setcartIDFromStorage(res.cart.cartId);
        this.cart.next(res.cart);
        this.cartID = res.cart.cartId;
        if (res.message) {
          this.eventer.emitMessageEvent(
            new EventMessage(res.message.type, res.message.title, res.message.body)
          );
        }
      }, error => {
        if (error.error) {
          if (error.error.cart) {
            this.setcartIDFromStorage(error.error.cart.cartId);
            this.cart.next(error.error.cart);
            this.cartID = error.error.cart.cartId;
          }
          /*this.eventer.emitMessageEvent(
           new EventMessage(error.error.message.type, error.error.message.title, error.error.message.body)
           );*/
        }
      });

  }

  setcartIDFromStorage(cartID) {
    localStorage.setItem('cartID', cartID);

  }

  userCart():Observable<any> {
    return this.cart;
  }

  setModifires(modifires, messages?:EventMessage[]):void {
    this.modifires.next(modifires);
    if (messages) this.modifiresMessage.next(messages);
  }

  getModifires():Observable<any> {
    return this.modifires;
  }


}
